import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { colorToString } from './utils/utils';
import { circlePath } from './utils/paths';
import { randomColor, colors, cols, rows, getColor } from './color';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

let circleRadius = 100;
const pathPoints = circlePath(circleRadius, circleRadius, circleRadius, 3, Math.PI * 1.5);
const pathPointsExtremity =  circlePath(circleRadius, circleRadius, circleRadius * 1.2, 3, Math.PI * 1.5);


const DARK_BLUE = '#161c5d';
const BG = '#e5e5e5';

const colorPoints = [
  {
    x: pathPoints[0].x,
    y: pathPoints[0].y,
    color: randomColor()
  },
  {
    x: pathPoints[1].x,
    y: pathPoints[1].y,
    color: randomColor()
  },
  {
    x: pathPoints[2].x,
    y: pathPoints[2].y,
    color: randomColor()
  }
];

const dpr = window.devicePixelRatio;
const offscreen = document.createElement('canvas');
offscreen.width = (circleRadius * 2) * dpr;
offscreen.height = (circleRadius * 2) * dpr;
const offscreenCtx = offscreen.getContext('2d');
offscreenCtx.scale(dpr, dpr);

function getShape() {
  offscreenCtx.save();

  offscreenCtx.beginPath();
  offscreenCtx.moveTo(pathPoints[0].x, pathPoints[0].y);

  for (let i = 1; i < pathPoints.length; i++) {
    offscreenCtx.lineTo(pathPoints[i].x, pathPoints[i].y);
  }
  offscreenCtx.clip()

  let size = circleRadius * 2;
  // let radius = diagonal(size, size);
  let radius = circleRadius * 2;

  for (let x = 0; x < size; x++) {
    for (let y = 0; y < size; y++) {
      offscreenCtx.fillStyle = colorToString(getColor(x, y, colorPoints, radius));
      offscreenCtx.fillRect(x, y, 1, 1);
    }
  }

  offscreenCtx.restore();

  return Promise.resolve(offscreen);
}

function drawOutlineTriangle() {
  ctx.save();
  // ctx.translate(WIDTH / 2, HEIGHT / 2);

  ctx.beginPath();
  ctx.lineWidth = 2;
  ctx.moveTo(pathPoints[0].x, pathPoints[0].y);
  for (let i = 1; i < pathPoints.length; i++) {
    ctx.lineTo(pathPoints[i].x, pathPoints[i].y);
  }
  ctx.closePath();
  ctx.strokeStyle = DARK_BLUE;
  ctx.stroke();

  ctx.restore();
}

function getLetter({ r, g, b }) {
  if (r > g && r > b) {
    return 'R';
  }
  if (g > r && g > b) {
    return 'G';
  }
  if (b > g && b > r) {
    return 'B';
  }

  return 'R';
}

function drawText(rotate) {
  // ctx.translate(WIDTH / 2, HEIGHT / 2);
  for (let i = 0; i < pathPoints.length; i++) {
    ctx.save();

    ctx.fillStyle = DARK_BLUE;
    ctx.font = "bold 8px helvetica";
    ctx.translate(pathPointsExtremity[i].x, pathPointsExtremity[i].y);
    ctx.rotate(-rotate);
    ctx.fillText(getLetter(colors[colorPoints[i].color]), -4, 0);
    ctx.restore();
  }
}

function drawDot(x, y) {
  ctx.save();
  ctx.translate(x, y);
  ctx.beginPath();
  ctx.arc(0, 0, 1, 0, Math.PI * 2);
  ctx.fill();
  ctx.restore();
}

function drawDotGrid(x, y, width, height) {
  ctx.save();
  ctx.translate(x, y);

  let xSize = width / cols;
  let ySize = height / rows;

  let currentSelected = colorPoints.map(c => c.color);

  for (let x = 0; x < cols; x++) {
    for (let y = 0; y < rows; y++) {
      let idx = ((y * rows) + x) % colors.length;

      if (currentSelected.includes(idx)) {
        ctx.fillStyle = colorToString(colors[idx]);
        ctx.fillRect(x * xSize, y * ySize, xSize, ySize);
      }

      ctx.fillStyle = DARK_BLUE;
      drawDot(x * xSize, y * ySize);

      if (x === cols - 1) {
        drawDot(x * xSize + xSize, y * ySize);
      }

      if (x === cols - 1 || y === rows - 1) {
        drawDot(x * xSize + xSize, y * ySize + ySize);
      }

      if (y === rows - 1) {
        drawDot(x * xSize, y * ySize + ySize);
      }
    }
  }

  ctx.restore();
}

let padding = WIDTH * 0.05;

app.onRender(({ frame, width, height, debug }) => {
  ctx.fillStyle = BG;
  ctx.fillRect(0, 0, width, height);

  drawDotGrid(padding, padding, width - (padding * 2), height - (padding * 2));

  ctx.save();
  ctx.translate(width / 2, height / 2);

  let r = (Math.PI * 2) * ((frame % 800) / 800)

  ctx.rotate(r);
  ctx.translate(-(shape.width / 4), -(shape.height / 4));
  ctx.drawImage(shape, 0, 0, shape.width / 2, shape.height / 2);


  drawOutlineTriangle();
  drawText(r);

  ctx.restore();

  if (frame % 120 === 0) {

    colorPoints[0].color = randomColor();
    colorPoints[1].color = randomColor();
    colorPoints[2].color = randomColor();

    getShape()
      .then(img => {
        shape = img;
      });
  }
});

let shape;

getShape()
  .then(img => {
    shape = img;
    app.start();
  });
